.nutrition__wrapper {
  @media (min-width: $desktop-width) {
    margin-left: 80px;
  }
}

.nutrition__text {
  font-weight: 400;
  font-size: 14px;
  line-height: lh(14, 14);
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: lh(20, 20);
  }

  @media (min-width: $desktop-width) {
    margin: 0 0 52px 0;
  }
}

.nutrition__btn-link {
  width: 280px;
}

.nutrition__list {
  @include list-reset;

  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 80px;
  }
}

.nutrition__item {
  background-color: var(--color-bg-blocks);
}

.nutrition__sub-title {
  font-weight: 400;
  font-size: 24px;
  line-height: lh(37, 24);
  text-transform: uppercase;
  margin: 0;
}

.nutrition__sub-text {
  font-family: var(--additional-font);
  font-weight: 400;
  font-size: 14px;
  line-height: lh(18, 14);
  color: var(--color-text-font);
  margin: 0;
}

.nutrition__link {
  font-weight: 400;
  font-size: 16px;
  line-height: lh(16, 16);
  text-decoration: none;
  text-transform: uppercase;
  color: var(--color-font);
}
